}

.sidebar {
  clear: both;
}
.sidebar::after {
  clear: both;
  content: '';
  display: table;
}
@media (min-width: 64em) {
  .sidebar {
    float: left;
    width: calc(200px - 1em);
    opacity: 0.75;
    -webkit-transition: opacity 0.2s ease-in-out;
    transition: opacity 0.2s ease-in-out;
  }
  .sidebar:hover {
    opacity: 1;
  }
  .sidebar.sticky {
    overflow-y: auto;
    max-height: calc(100vh - 2em - 2em);
  }
}
@media (min-width: 80em) {
  .sidebar {
    width: calc(300px - 1em);
  }
}
.sidebar > * {
  margin-top: 1em;
  margin-bottom: 1em;
}
.sidebar h2,
.sidebar h3,
.sidebar h4,
.sidebar h5,
.sidebar h6 {
  margin-bottom: 0;
